import React from "react";

interface ExtendedVideoProps
  extends React.VideoHTMLAttributes<HTMLVideoElement> {
  id: string;
}

const FullScreenVideo: React.FC<ExtendedVideoProps> = ({
  id,
  src,
  className,
  preload = "metadata", // Default value for preload
  ...rest
}) => (
  <video id={id} src={src} className={className} controls preload={preload} {...rest}>
    Your browser does not support the video tag.
  </video>
);

export default FullScreenVideo;
